import React from 'react';
import Axios from 'axios';
import Alert from 'react-s-alert';
import { RadioGroup, RadioButton } from 'react-radio-buttons';

export default class NewParent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            student: '',

            working: "",
            instagram: "",
            ownSmartphone: "",
            education: ""
        }
    }

    submitData() {
        if (!this.state.name) return Alert.warning("      ", { position: 'top-right', })
        if (!this.state.student) return Alert.warning("      ", { position: 'top-right', })
        if (!this.state.instagram) return Alert.warning("      ", { position: 'top-right', })
        if (!this.state.working) return Alert.warning("      ", { position: 'top-right', })
        if (!this.state.ownSmartphone) return Alert.warning("      ", { position: 'top-right', })
        if (!this.state.education) return Alert.warning("      ", { position: 'top-right', })

        Axios({
            method: "POST",
            url: global.apiHost + '/api/parent/add',
            data: {
                name: this.state.name,
                student: this.state.student,
                working: (this.state.working == 'true'),
                instagram: (this.state.instagram == 'true'),
                ownSmartphone: (this.state.ownSmartphone == 'true'),
                education: this.state.education
            }
        }).then((response) => {
            Alert.success(`${this.state.name}    `)
            window.location.reload();
        }).catch((err) => {
            Alert.error("", { position: 'top-right', })
        })
    }

    render() {
        return (
            <div>
                <div className="center-content" style={{ paddingBottom: 40 }} >
                    <div className="container" >
                        <h1 className="text-center F6 darkgray" style={{ marginTop: 20 }} >   </h1>
                        <p className="red text-center" >*        ""  *</p>
                        <div className="row" style={{ justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }} >
                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >   </label>
                                <input value={this.state.name} onChange={(value) => this.setState({ name: value.target.value })} style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="   " />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >    </label>
                                <input value={this.state.student} onChange={(value) => { this.setState({ student: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="    " />
                            </div>

                            <div className="" style={{ marginTop: 20 }}>
                                <label> </label>
                                <RadioGroup onChange={(value) => this.setState({ education: value })} horizontal>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value=" ">
                                        
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="">
                                        
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="">
                                        
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="">
                                        
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value=" ">
                                         
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="">
                                        
                                    </RadioButton>
                                </RadioGroup>
                            </div>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label> </label>
                            <RadioGroup horizontal onChange={(value) => this.setState({ ownSmartphone: value })}>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    
                                    </RadioButton>
                            </RadioGroup>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label></label>
                            <RadioGroup onChange={(value) => this.setState({ instagram: value })} horizontal>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    
                                    </RadioButton>
                            </RadioGroup>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label></label>
                            <RadioGroup onChange={(value) => this.setState({ working: value })} horizontal>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    
                                    </RadioButton>
                            </RadioGroup>
                        </div>
                        <div className="center-content" style={{ marginTop: 20 }} >
                            <button className="btn btn-primary" onClick={() => { this.submitData() }} > </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


